/* ******* Gallery ******* */


#gallery h3 {
	font-family: 'Lato', sans-serif;
	text-transform: none;
	color: #182c39;
	margin: 50px auto 10px auto;
	font-weight: 700;
}

#gallery .gallery {
	margin: 0px 80px;
}

#gallery .no-gutter > [class*="col-"] {
	padding-right: 0;
	padding-left: 0;
}

#gallery .caption-content {	
	position: absolute;
	bottom: 100%;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.8); 
    color: #f1f1f1;
	overflow: hidden;
	width: 100%;
	height: 0;
	transition: .5s ease;
}

#gallery .portfolio-item:hover .caption-content {
	bottom: 0;
	height: 100%;
}

#gallery .caption-content .fa {
	white-space: nowrap;
	color: white;
	font-size: 40px;
	position: absolute;
	overflow: hidden;
	top: 50%;
	left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
	-moz-transform: translate(-50%, -50%);
	-o-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	
}

#gallery .more-img {
	margin: 0 80px;
	background-color: #182c39;
}

#gallery .more-img .to-gallery {
	text-align: center;
	text-transform: none;
	color: #fff;
	padding: 10px;
	margin: 0px auto;
}

#gallery .more-img .to-gallery h4 {
	text-transform: none;
	color: #fff;
	padding: 5px;
	margin: auto;
	font-size: 16px;
}

#gallery .more-img .social-networks .fa {
	font-size: 20px;
	margin: 5px 5px;
	color: #fff;
}

#gallery .more-img .social-networks .fa:hover {
	color: #ffc266;
}





/* ******* Testimonials ******* */
#testimonials .hr-testimonials {
