.container-name{
	background-color: gray;
	width: 100%;
}
.bg-tabs {
	margin: auto 20px;
}

#tabs .nav-pills {
	border-color: transparent;
	font-weight: 500;
	color: #000;
	margin: 25px;
}

#tabs .nav-pills > li > a,
#tabs .nav-pills > li > a:focus {
	font-weight: 500;
	font-size: 15px;
	color: #fff;
	border-radius: 0;
	border: 1px solid #fff;
	text-transform: uppercase;
	font-family: 'Roboto', sans-serif;

}

#tabs .nav-pills > li > a:hover,
#tabs .nav-pills > li > a:focus:hover {
	color: #fff;
	background-color: #944743;
}

#tabs .nav-pills > li > a.active,
#tabs .nav-pills > li  > a.active:focus {
	background-color: blue;
	color: #fff;
}


.tab-content{
	width: 100%;
}

#tabs .hr-tabs {
	border: 2px solid #182c39;
	width: 100%;
}

#tabs .list-group-item {
	background-color: transparent;
	border: none;
	border-bottom: 1px solid #555;
	padding: 0 auto;
}

#tabs .tab-content  .tab-pane  h3 {
	text-align: center;
	font-family: 'Leckerli One', cursive;
}

#tabs .list-group-item:first-child {
	border-top-right-radius: 0;
	border-top-left-radius: 0;
}

#tabs .list-group-item:last-child {
	border-bottom-right-radius: 0;
	border-bottom-left-radius: 0;
}

#tabs .list-group-item  h4 {
	text-align: left;
	color: #182c39;
	text-transform: none;
	font-size: 18px;
}

#tabs .list-group-item  p {
	text-align: left;
	color: #555555;
	font-weight: 500;
	font-style: italic;
	text-transform: none;
	font-size: 14px;
}

#tabs .badge {
	border-radius: 0;
	background-color: #444;
	font-size: 12px;
	color: #fff;
}

#tabs .right-cover {
	background-color: #fff;
	color: #DC4C46;
	text-shadow: #DC4C46 .4em .4em .4em;
	padding: 10px;
	text-transform: none;
}